import './ActionButtons.css';

const ActionButton = (props) => {
  let classes = `ActionButton ${props.className}`;
  if (props.isActive) {
    classes += " active";
  }
  return ( 
    <button 
      className={classes}
      onClick={props.onClick}>
      {props.label}
    </button>
  );
}

export const ListButton = (props) => {
  return ( 
    <ActionButton 
      className="ListButton"
      label="&#9776;"
      isActive={props.isActive}
      onClick={props.onClick}
    />
  );
}

