import React, { useState, useEffect, useMemo } from 'react';
import ListBirths from './ListBirths.js';
import AddBirth from './AddBirth.js';
import Upload from './Upload.js';
import { Fetch } from './Utils.js';
import { ListButton, AddButton, UploadButton, DownloadButton } from './ActionButtons';

export const Birth = () => {
  const [screen, setScreen] = useState("list");
  const [areas, setAreas] = useState([]);

  useEffect(() => {
    Fetch("api/data/areas", "get", null,
      (data) => { setAreas(data); },
      () => { console.log("error loading areas"); }
    );
  }, []);

  const birthDataModel = useMemo(() => [
    { field: "tag",   type: "number", descriptionKey: "upload_desc_tag",   range: [0, 99999999] },
    { field: "breed", type: "enum",   descriptionKey: "upload_desc_breed", values: ["angus","nelore","brangus","sta_zelia","cruzada","murrah","mediterrneo","jafarabadi","carabao","marchigiana","charolais","sindi"] },
    { field: "sex",   type: "enum",   descriptionKey: "upload_desc_sex",   values: ["m","f"] },
    { field: "area",  type: "enum",   descriptionKey: "upload_desc_area",  values: areas.map(a => a.name) },
    { field: "cause", type: "enum",   descriptionKey: "upload_desc_cause", values: ["viva","aborto","morreu","morto","nasceu-morto","natimorto","natimortos","other"] },
    { field: "date",  type: "date",   descriptionKey: "upload_desc_date" },
  ], [areas]);

  return (
    <>
      <div className="content-header">
        <div className="action-buttons">
          <ListButton 
            isActive={screen=="list"}
            onClick={() => setScreen("list")}
          />
          <AddButton 
            isActive={screen=="add"}
            onClick={() => setScreen("add")}
          />
          <UploadButton 
            isActive={screen=="upload"}
            onClick={() => setScreen("upload")}
          />
          <DownloadButton collection="births"/>
        </div>
      </div>

      {(screen === "list") && <ListBirths/>}
      {(screen === "add") && 
        <AddBirth 
          onSuccess={() => setScreen("list")}
        />
      }
      {(screen === "upload") && 
        <Upload collection="births"
          dataModel={birthDataModel}
          onSuccess={() => setScreen("list")}
        />
      }
    </>
  );
}

export default Birth;
