import React, { useState, useEffect } from 'react';
import DataCollection from './DataCollection';
import BreedDropdown from './BreedDropdown';
import SexDropdown from './SexDropdown';
import AreaDropdown from './AreaDropdown';
import DeathCauseDropdown from './DeathCauseDropdown';
import TagNumberInput from './TagNumberInput';
import DateInput from './DateInput.js';
import Patch from "./Patch.js";
import { useTranslation } from 'react-i18next';
import { daysSince, Fetch } from './Utils.js';
import './ListHerd.css';

const ageCategoryToDateRegex = (category) => {
  const now = new Date();
  let startMonths, endMonths;
  switch (category) {
    case "0-12":   startMonths = 0;  endMonths = 12; break;
    case "13-24":  startMonths = 13; endMonths = 24; break;
    case "25-36":  startMonths = 25; endMonths = 36; break;
    case "37+":    startMonths = 37; endMonths = 72; break;
    default: return null;
  }
  const parts = [];
  for (let m = startMonths; m <= endMonths; m++) {
    const d = new Date(now.getFullYear(), now.getMonth() - m, 1);
    const yyyy = d.getFullYear();
    const mm = String(d.getMonth() + 1).padStart(2, '0');
    parts.push(`${yyyy}-${mm}`);
  }
  return `^(${parts.join('|')})`;
};

const DateToAgeCategory = (date) => {
  // Roughly 30 days per month.
  const months = Math.floor(daysSince(date)/30);
  if (months <= 12) {
    return "0-12 months";
  }
  if (months >= 13 && months <= 24) {
    return "13-24 months";
  }
  if (months >= 25 && months <= 36) {
    return "25-36 months";
  }
  return "37+ months";
}

export const ListBirths = () => {
  const [refreshKey, setRefreshKey] = useState(0)
  const [areas, setAreas] = useState([])
  const [searchFields, setSearchFields] = useState({})

  useEffect(() => {
    Fetch("api/data/areas", "get", null, 
      (data) => {
        setAreas(data);
        setRefreshKey(refreshKey => refreshKey + 1);
      },
      () => {console.log("error loading areas")}
    );
  }, []);

  const onChange = (e, field, id) => {
    let value = e.target.value;
    if (value === "true") {
      value = true;
    }
    if (value === "false") {
      value = false;
    }

    if (field === "tag") {
      value = parseInt(value, 10);
    }

    Patch("births", id, field, value,
      () => { 
        console.log("success");
        setRefreshKey(refreshKey => refreshKey + 1);
      },
      () => { console.log("error")},
    );
  }

  const { t } = useTranslation();
  const collection = 'births';
  const columns = [
    {
      name: t("tag"),
      sortable: true,
      selector: row => row.tag,
      cell: row => <TagNumberInput
        edit={false}
        value={row.tag}
        onBlur={(e) => {onChange(e, "tag", row._id)}}
      />
    },

    {
      name: t("breed"),
      sortable: true,
      selector: row => row.breed,
      cell: row => <BreedDropdown
        selected={row.breed}
        onChange={(e) => {onChange(e, "breed", row._id)}}
      />,
    },

    {
      name: t("sex"),   
      sortable: true,
      selector: row => row.sex,
      cell: row => <SexDropdown
        selected={row.sex}
        onChange={(e) => {onChange(e, "sex", row._id)}}
      />
    },

    {
      name: t("age"),
      sortable: true,
      selector: row => DateToAgeCategory(row.date),
    },

    {
      name: t("area"),  
      sortable: true,
      selector: row => row.area,
      cell: row => <AreaDropdown
        selected={row.area}
        areas={areas}
        showUnknown={true}
        onChange={(e) => {onChange(e, "area", row._id)}}
      />
    },

    {
      name: t("cause"),  
      sortable: true,
      selector: row => row.cause,
      cell: row => <DeathCauseDropdown
        selected={row.cause}
        onChange={(e) => {onChange(e, "cause", row._id)}}
      />
    },

    {
      name: t("birth_date"),  
      selector: row => row.date.substring(0,10), 
      sortable: true,
      cell: row => <DateInput
        date={row.date.substring(0,10)}
        onChange={(value) => {
          Patch("births", row._id, "date", value,
            () => {console.log("success")},
            () => {console.log("error")},
          );
        }}
      />
    },

    {
      name: t("who"),
      selector: row => row.created_by,
      sortable: true
    },

    {
      name: t("from"),
      selector: row => row.phone,
      sortable: true
    },
  ];

  const onSearchTag = (e) => {
    const value = e.target.value;
    setSearchFields(prev => {
      const next = {...prev};
      if (value) {
        next.tag = value;
      } else {
        delete next.tag;
      }
      return next;
    });
  };

  const onSearchBreed = (e) => {
    const value = e.target.value;
    setSearchFields(prev => {
      const next = {...prev};
      if (value) {
        next.breed = value;
      } else {
        delete next.breed;
      }
      return next;
    });
  };

  const onSearchArea = (e) => {
    const value = e.target.value;
    setSearchFields(prev => {
      const next = {...prev};
      if (value) {
        next.area = value;
      } else {
        delete next.area;
      }
      return next;
    });
  };

  const onSearchCause = (e) => {
    const value = e.target.value;
    setSearchFields(prev => {
      const next = {...prev};
      if (value) {
        next.cause = value;
      } else {
        delete next.cause;
      }
      return next;
    });
  };

  const onSearchSex = (e) => {
    const value = e.target.value;
    setSearchFields(prev => {
      const next = {...prev};
      if (value) {
        next.sex = value;
      } else {
        delete next.sex;
      }
      return next;
    });
  };

  const onSearchAge = (e) => {
    const value = e.target.value;
    setSearchFields(prev => {
      const next = {...prev};
      if (value) {
        const regex = ageCategoryToDateRegex(value);
        if (regex) {
          next.date = regex;
        }
      } else {
        delete next.date;
      }
      return next;
    });
  };

  return (
    <div className="ListHerd">
      <div className="filters">
        <input
          type="number"
          placeholder={t("find_tag")}
          onChange={onSearchTag}
        />
        <BreedDropdown
          showAll={true}
          onChange={onSearchBreed}
        />
        <SexDropdown
          showAll={true}
          onChange={onSearchSex}
        />
        <select
          onChange={onSearchAge}
        >
          <option value="">{t("all_ages")}</option>
          <option value="0-12">0-12 {t("months")}</option>
          <option value="13-24">13-24 {t("months")}</option>
          <option value="25-36">25-36 {t("months")}</option>
          <option value="37+">37+ {t("months")}</option>
        </select>
        <AreaDropdown
          showAll={true}
          areas={areas}
          selected={searchFields.area || ""}
          onChange={onSearchArea}
        />
        <DeathCauseDropdown
          showAll={true}
          onChange={onSearchCause}
        />
      </div>
      <DataCollection
        key={refreshKey}
        collection={collection}
        columns={columns}
        searchFields={searchFields}/>
    </div>
  );
}
export default ListBirths;
