import './AnimalsProtocolStatus.css';
import { useTranslation } from 'react-i18next';
import React, { useState } from 'react';
import Patch, { PatchV2 } from './Patch.js';
import Days from './Days.js';

export const AnimalsProtocolStatus = (props) => {
  const { t } = useTranslation();

  const Checkbox = (props) => {
    const [isChecked, setIsChecked]  = useState(props.checked);
    const onChange = (e) => {
      Patch ("reproduction.active", 
        props.id, props.field, e.target.checked,
        () => {
          setIsChecked(e.target.checked);
          props.onUpdate();
        }
      );
    }

    if (isChecked) {
      return (
        <div className="Checkbox">
          <input type="checkbox" 
            value="completed"
            onChange={onChange} 
            checked
          /> 
          <span>Completed</span>
        </div>
      );
    }

    return (
      <div className="Checkbox">
        <input type="checkbox" 
          value="not-completed"
          onChange={onChange} 
        /> 
        <span>Not Completed</span>
      </div>
    );
  }

  const isComplete = (animal, dayIndex, treatmentIndex) => {
    if (animal.protocol == undefined) { return false }
    if (animal.protocol == null) { return false }
    if (animal.protocol.timeline_days == undefined) { return false }
    if (animal.protocol.timeline_days == null) { return false }
    if (animal.protocol.timeline_days[dayIndex] == undefined) { return false }
    if (animal.protocol.timeline_days[dayIndex] == null) { return false }
    if (animal.protocol.timeline_days[dayIndex].completed == undefined) { return false }
    if (animal.protocol.timeline_days[dayIndex].completed == null) { return false }
    const completed = animal.protocol.timeline_days[dayIndex].completed;
    return animal.protocol.timeline_days[dayIndex].completed;
  }

  return (
    <div className="AnimalsProtcolStatus">
      <div className="name">{props.protocol.name}</div>
      <div className="desc">{props.protocol.description}</div>
      {props.protocol.timeline_days.map((day, index) => (
        <div className="timeline">
          <div>
            <Days 
              start={day.start_day}
              end={day.end_day}
            />
            <span>{day.event}</span>
          </div>
          <Checkbox
            id={props.animal._id}
            field={`protocol.timeline_days.${index}.completed`}
            checked={isComplete(props.animal, index)}
            onUpdate={props.onUpdate}
          />
          {props.protocol.timeline_days[index].treatments &&
          <div className="treatments-container">
            <div className="treatments-header">
              {t("treatments")}:
            </div>
            {props.protocol.timeline_days[index].treatments.map((treatment, treatmentIndex) => (
              <Treatment treatment={treatment}/>
            ))}
          </div>
          }
        </div>
      ))}
    </div>
  );
}

export const Treatment = (props) => {
  const { t } = useTranslation();
  return (
    <div className="Treatment">

      {props.treatment.medication &&
      <div className="medication">
        {t("medication")}: {props.treatment.medication}
      </div>
      }

      {props.treatment.device &&
      <div className="device">
        {t("device")}: {props.treatment.device}
      </div>
      }

      {props.treatment.procedure &&
      <div className="procedure">
        {t("proceedure")}: {props.treatment.procedure}
      </div>
      }

      {props.treatment.notes &&
      <div className="notes">
        {t("notes")}: {props.treatment.notes}
      </div>
      }
    </div>
  );
}

export default AnimalsProtocolStatus;
